<template>
  <div class="info-wrap">
    <app-header
      title="车辆信息"
      showBack="true"
    ></app-header>
    <div class="info-main-wrap">
      <div class="car">
        <i class="fa fa-truck" aria-hidden="true"></i>
        <div class="num">
          <span>车牌号: {{car.vehicle_license_province}} {{car.vehicle_license_number}}</span>
          <span>车架号: {{car.vin}}</span>
        </div>
      </div>
      <div class="detail">
        <p><span>品牌:</span>{{car.vehicle_brand}}</p>
        <p><span>设备号:</span>{{sim}}</p>
      </div>
    </div>
  </div>
</template>

<script>
import AppHeader from '../../../../components/header/header.vue'
import {getLocalStorage} from '../../../../lib/util'
export default {
  name: 'Car-Info',
  data () {
    return {
      car: '',
      sim: ''
    }
  },
  created () {
    this.getDatas()
  },
  methods: {
    getDatas () {
      this.$set(this, 'car', JSON.parse(getLocalStorage('vle')))
      this.$set(this, 'sim', this.$route.params.id)
    }
  },
  components: {
    AppHeader
  }
}
</script>

<style lang="less" scoped>
  .info-wrap{
    height: 100%;
    .info-main-wrap{
      width: 100%;
      height: 100%;
      position: fixed;
      padding-top: 50px;
      overflow: hidden;
      .car{
        display: flex;
        text-align: center;
        margin: .7rem;
        margin-top: 0;
        padding: .4rem 0;
        background-color: #fff;
        i {
          display: block;
          flex: 1;
          font-size: 3rem;
        }
        .num{
          flex: 1;
          text-align: left;
          span{
            display: block;
          }
        }
      }
      .detail{
        padding: .2rem;
        background-color: #fff;
        p{
          margin: 0;
          padding: .7rem .6rem;
          border-bottom: 1px solid #ddd;
        }
      }
    }
  }
</style>



